.welcome-container {
  min-height: 100vh;
  color: #1f1f1f;
  background: radial-gradient(circle at 10% 20%, #e9e3ff, #b5fffc);
  background-size: 200% 200%;
  animation: gradientShift 10s ease infinite;
}
.welcome-container .hero-section {
  position: relative;
  z-index: 1;
  padding: 100px 20px 100px;
  text-align: center;
}
.welcome-container .hero-section .hero-title {
  font-weight: 800;
  font-size: 3rem;
  letter-spacing: -1px;
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.welcome-container .hero-section .hero-subtitle {
  margin-top: 12px;
  color: rgba(0, 0, 0, 0.6);
  font-size: 1.2rem;
}
.welcome-container .hero-section .hero-title,
.welcome-container .hero-section .hero-phrase,
.welcome-container .hero-section .hero-subtitle {
  position: relative;
  z-index: 2;
}
.welcome-container .hero-section::after {
  position: absolute;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  content: '';
  pointer-events: none;
  inset: 0;
}
/* Hero container */
.hero-phrase-container {
  position: relative;
  height: 40px;
  margin-top: 10px;
  margin-bottom: 20px;
  overflow: hidden;
}
.hero-phrase {
  color: #111;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 40px;
  letter-spacing: -0.5px;
  text-align: center;
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.blob-container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.blob-container .blob {
  position: absolute;
  height: 400px;
  border-radius: 50%;
  opacity: 0.6;
  filter: blur(100px);
  mix-blend-mode: screen;
  animation: blobMove 10s infinite ease-in-out;
}
.blob-container .blob1 {
  top: 10%;
  left: 20%;
  background: radial-gradient(circle at center, #ff718b, #ffeced);
  animation-delay: 0s;
}
.blob-container .blob2 {
  top: 40%;
  right: 15%;
  background: radial-gradient(circle at center, #87e6fb, #6f9eff);
  animation-delay: 5s;
}
.blob-container .blob3 {
  bottom: 10%;
  left: 10%;
  background: radial-gradient(circle at center, #b5fffc, #ffdee9);
  animation-delay: 10s;
}
.gradient-text {
  background: linear-gradient(90deg, #ff718b, #ffeced);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero-section {
  text-align: center;
  padding: 120px 24px 80px;
  position: relative;
}
.hero-section .hero-title {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 20px;
}
.hero-section .hero-phrase {
  font-size: 1.4rem;
  color: #444;
  margin-bottom: 8px;
}
.hero-section .hero-subtitle {
  max-width: 640px;
  margin: 0 auto;
  color: #666;
}
/* üß± Story Sections */
.story-section {
  max-width: 1200px;
  margin: 100px auto;
  padding: 40px 24px;
  display: flex;
  align-items: center;
}
.story-section.reverse .ant-row {
  flex-direction: row-reverse;
}
.story-section .story-text h2 {
  font-size: 2.2rem;
  margin-bottom: 12px;
}
.story-section .story-text .story-desc {
  font-size: 1rem;
  color: #555;
  margin-bottom: 24px;
}
.story-section .story-preview .story-card {
  background: rgba(255, 255, 255, 0.35);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  transition: all 0.35s ease;
}
.story-section .story-preview .story-card:hover {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  transform: translateY(-6px) scale(1.03);
}
.story-section .story-preview .story-card:hover .story-icon {
  transform: rotate(6deg) scale(1.1);
}
.story-section .story-preview .story-card .story-icon {
  font-size: 2rem;
  margin-bottom: 10px;
  transition: transform 0.3s ease;
}
.story-section .story-preview .story-card .story-card-desc {
  color: #777;
  font-size: 0.9rem;
}
@media (max-width: 768px) {
  .story-section .story-preview .story-card .story-name {
    font-size: 0.6rem;
  }
}
.story-carousel .slick-slide {
  padding: 20px 10px;
}
.story-carousel .slick-dots li button {
  background: #ccc;
}
.story-carousel .slick-dots li.slick-active button {
  background: #ff718b;
}
/* ‚ú® Testimonial */
.testimonial-section {
  background: linear-gradient(90deg, #ffeced, #fff);
  padding: 80px 24px;
  text-align: center;
  border-radius: 24px;
  max-width: 900px;
  margin: 140px auto 0;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.05);
}
.testimonial-section blockquote {
  font-size: 1.4rem;
  font-style: italic;
  color: #333;
  margin-bottom: 16px;
}
.testimonial-section .author {
  color: #666;
}
.testimonial-section .author strong {
  display: block;
  font-weight: 600;
}
/* Playground */
.playground-section {
  width: 100%;
  padding: 75px 6vw;
  margin-bottom: 60px;
  position: relative;
  overflow: hidden;
  border-radius: 0;
  background: linear-gradient(135deg, #fdfbfb 0%, #ebedee 100%);
  border-radius: 12px;
  /* üñºÔ∏è Subtle background image with blending */
  /* üåà Animated glowing gradient overlay */
}
.playground-section::after {
  content: '';
  position: absolute;
  inset: 0;
  background-image: url('https://images.unsplash.com/photo-1519389950473-47ba0277781c?auto=format&fit=crop&w=1600&q=80');
  background-size: cover;
  background-position: center;
  opacity: 0.15;
  /* adjust visibility */
  z-index: 0;
  mix-blend-mode: multiply;
  transform: scale(1.05);
}
.playground-section::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 10% 10%, #ff718b20, #41b3ff10);
  animation: glowMove 10s ease-in-out infinite;
  z-index: 1;
}
.playground-section .playground-header {
  position: relative;
  z-index: 2;
  text-align: center;
  margin-bottom: 60px;
}
.playground-section .playground-header .playground-title {
  font-size: 2.5rem;
  font-weight: 800;
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.playground-section .playground-header .playground-subtitle {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.65);
  margin-top: 10px;
}
.playground-section .playground-cards {
  position: relative;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
}
.playground-section .playground-card {
  flex: 1 1 300px;
  max-width: 360px;
  background-color: transparent;
  box-shadow: none;
}
.playground-section .playground-card .playground-card-inner {
  padding: 28px 24px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
}
.playground-section .playground-card .playground-card-inner:hover {
  backdrop-filter: blur(20px) saturate(180%);
  /* slightly stronger blur for clarity */
  background: rgba(255, 255, 255, 0.75);
  transform: translateY(-6px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.3);
}
.playground-section .playground-card .playground-card-inner .playground-icon {
  font-size: 2.5rem;
  margin-bottom: 16px;
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.playground-section .playground-card .playground-card-inner .playground-desc {
  min-height: 50px;
  color: rgba(0, 0, 0, 0.7);
}
.playground-section .playground-card .playground-card-inner .ant-btn {
  margin-top: 10px;
  border-radius: 10px;
  font-weight: 600;
  transition: all 0.3s ease;
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  color: #fff;
  border: none;
}
.playground-section .playground-card .playground-card-inner .ant-btn:hover {
  filter: brightness(1.1);
  transform: scale(1.05);
}
@keyframes glowMove {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
@media (max-width: 768px) {
  .playground-section {
    padding: 50px 4vw;
  }
  .playground-section .playground-header {
    margin-bottom: 40px;
  }
  .playground-section .playground-header .playground-title {
    font-size: 1.8rem;
  }
  .playground-section .playground-header .playground-subtitle {
    font-size: 1rem;
    margin-top: 8px;
  }
  .playground-section .playground-cards {
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }
  .playground-section .playground-card {
    width: 100%;
    max-width: 320px;
    transform: scale(0.95);
    transition: transform 0.3s ease;
  }
  .playground-section .playground-card:hover {
    transform: scale(0.98);
  }
  .playground-section .playground-card .playground-card-inner {
    padding: 20px 16px;
    border-radius: 14px;
  }
  .playground-section .playground-card .playground-card-inner .playground-icon {
    font-size: 2rem;
  }
  .playground-section .playground-card .playground-card-inner h4 {
    font-size: 1.1rem;
  }
  .playground-section .playground-card .playground-card-inner .playground-desc {
    font-size: 0.9rem;
    margin: 8px 0;
  }
  .playground-section .playground-card .playground-card-inner .ant-btn {
    font-size: 0.9rem;
    padding: 6px 14px;
  }
}
.ant-pro-layout .ant-pro-layout-content {
  padding-inline: 0px;
  padding-block: 0px;
}
